body{
    &.page-template-template-testimonials {
        section.main-section {
            @media only screen {

              h2{
                &.logo-headline{
                  .color-blue;
                  font-size: 22px;
                  text-align: center;
                }
              }

              .testimonials{
                list-style-type: none;
                margin: 0;
                padding: 0;

                .testimonial{
                  background: url('../images/quotes.png') 30px 30px no-repeat;
                  background-size: 50px 50px;
                  border: @lt-grey solid 1px;
                  padding: 90px 30px 30px 30px;
                  margin-bottom: 30px;

                  &.featured{
                    .background-blue;
                    .color-white;
                  }

                  &:last-of-type{
                    margin-bottom: 0;
                  }
                }
              }
            }

            @media only screen and (min-width: @screen-sm-min) {
              .testimonials{
                .testimonial{
                }
              }
            }

            @media only screen and (min-width: @screen-md-min) {
              .testimonials {
                .testimonial {
                  padding: 30px 30px 30px 100px;
                }
              }
            }

            @media only screen and (min-width: @screen-lg-min) {

            }
        }
    }
}