#sidebar {
  @media only screen and (min-width: 1px) {
    .background-lt-green;
    padding: 20px;
    margin-bottom: 15px;

    h3{
      font-size: 18px;
      line-height: 1.25em;
      margin: 0 0 10px 0;
      .color-white;
    }

    select {
      display:none;
      .roboto-slab;
      .color-white;
      font-weight: 400;
      .font-1;
      width: 100%;
      outline: none !important;
      .lh-appearance(none);
      border: none;
      background: #95d759 url('../images/chevrons/chevron-dropdown-bg.png') 95% center no-repeat;
      background-size: 20px 15px;
      padding: 5px 5px 5px 15px;
      .lh-border-radius(0);
    }
  }

  @media only screen and (min-width: @screen-sm-min) {
  }

  @media only screen and (min-width: @screen-md-min) {
    .background-transparent;
    margin-bottom: 0;

    h3{
      margin: 0 -20px;
      .background-blue;
      padding: 5px 10px 5px 40px;
      font-size: 16px;
    }

    ul {
      list-style: none;
      padding: 0;
      margin: 10px 0 10px 20px;

      li {
        .color-lt-green;

        a {
          .color-lt-green;
        }

        .chevron {
          position: absolute;
          left: 13px;
        }
      }
    }
  }

  @media only screen and (min-width: @screen-lg-min) {
  }
}